<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-input v-model="article.title" placeholder="请输入文章标题" suffix-icon="el-icon-edit"></el-input>
      </el-col>
    </el-row>
    <div style="margin: 20px 0;"></div>
    <el-row>
      <el-col :span="24">
        <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 15}" placeholder="请输入文章内容" v-model="article.articleContent"></el-input>
      </el-col>
    </el-row>
    <div style="margin: 20px 0;"></div>
    <el-row type="flex" justify="center">
      <el-col :span="12">
        <el-button type="success" @click="Post()" round>发表文章</el-button>
      </el-col>
      <el-col :span="12">
        <el-button type="primary" @click="back()" round>返回列表</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Qs from "qs";
export default {
  data() {
    return {
      article: {
        title: "",
        staffID: sessionStorage.getItem('staffID'),
        depID: sessionStorage.getItem('depID'),
        articleContent: ""
      }
    };
  },
  methods: {
    Post() {
      // this.article.staffID = sessionStorage.getItem('staffID');
      // this.article.depID = sessionStorage.getItem('depID');
      console.log("post对象：" + JSON.stringify(this.article));
      var api = "http://localhost:8888/article/add";
      this.axios
        .post(api, Qs.stringify(this.article))
        .then(response => {
          console.log(response);
          if (response.data == 1) {
            this.$message({
              showClose: true,
              message: "操作成功",
              type: "success"
            });
            this.$router.push({
              name: "article",
              params: { current: 1, size: 5 }
            });
          }
        })
        .catch(error => {
          console.log(error);
          this.$message({
            showClose: true,
            message: "操作失败，请联系维护人员",
            type: "error"
          });
        });
    },
    back() {
        this.$router.push({
              name: "article",
              params: { current: 1, size: 5 }
            });
    }
  }
};
</script>

<style lang="scss" scoped>
</style>